<template>
  <div class="payBody">
    <Service v-show="serviceShow" v-on:childSay="servicehide"></Service>
    <router-view></router-view>
    <div class="user-header">
      <a class="nav-item1" @click="payBodyBack"><img class="game_1" src="../../assets/images/usercenter/game_1.png"/></a>
      <a class="wode" >充值结果</a>
      <a @click="lian" class="history">联系客服</a>
    </div>
    <div class="payMain" v-show="paysuccess">
      <div class="success">
        <div class="stitle">
          <img src="../../assets/images/usercenter/bc_43.png"/>
          <a>支付成功!</a>
        </div>
        <div class="content">您购买的钻石支付成功!</div>
      </div>
      <div class="options">
        <ul>
          <li @click="seen">查看订单</li>
          <li @click="backroom">返回游戏大厅</li>
        </ul>
      </div>
    </div>
    <div class="paylose" v-show="paylose">
      <div class="success">
        <div class="stitle">
          <img src="../../assets/images/usercenter/bc_44.png"/>
          <a>支付失败!</a>
        </div>
        <div class="content">支付失败，请重新支付，或更换支付方式!</div>
      </div>
      <div @click="againpay" class="againpay">
        <a>重新购买</a>
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
  @function n($n) {
    @return $n  / 64 + rem;
  };
  .payBody {

    .user-header{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: n(80);
      background: #fff001;
      display: flex;
      justify-content: space-between;
      align-items: center;
      z-index: 999;
      border-bottom: 1px solid #d8d8d8;
      .nav-item1{
        .game_1{
          margin-left: n(23);
          width: n(50);
          height: n(50);
        }
      }
      .wode{
        font-size: 14px;
        font-weight: bold;
      }
      .history{
        margin-right: n(23);

      }
    }
    .payMain{
      margin-top: n(80);
      width: 100%;
      overflow: hidden;
      .success{
        width: 50%;
        margin-left: 25%;
        height: n(120);
        margin-top: n(200);
        .stitle{
          width: 100%;
          display: inline-flex;
          justify-content: center;
          align-items: center;
          img{
            width: n(44);
            height: (44);
          }
          a{
            margin-left: n(10);
            font-size: n(24);
            font-weight: bold;
          }
        }
        .content{
          color: #b6b6b6;
          text-align: center;
        }
      }
      .options{
        width: 80%;
        margin-left: 10%;
        margin-top: n(200);
        ul{
          width: 100%;
          display: inline-flex;
          justify-content: space-between;
          li{
            width: 45%;
            height: n(80);
            background: #fff001;
            text-align: center;
            line-height: n(80);
          }
        }
      }
    }
    .paylose{
      margin-top: n(80);
      width: 100%;
      overflow: hidden;
      .success{
        width: 50%;
        margin-left: 25%;
        height: n(120);
        margin-top: n(200);
        .stitle{
          width: 100%;
          display: inline-flex;
          justify-content: center;
          align-items: center;
          img{
            width: n(44);
            height: (44);
          }
          a{
            margin-left: n(10);
            font-size: n(24);
            font-weight: bold;
          }
        }
        .content{
          color: #b6b6b6;
          text-align: center;
        }
      }
      .againpay{
        margin-top: n(200);
        width: 60%;
        height: n(80);
        background: #fff001;
        text-align: center;
        line-height: n(80);
        margin-left: 20%;
      }
    }
  }

</style>
<script>
  import Service from '../batai/service.vue'
  var urlhost = process.env.API_AJAX;
    export default {
        data() {
            return {
              paysuccess:false,
              paylose:false,
              serviceShow:false,
            }
        },
        components: {
          Service,
        },
      mounted:function () {
          var _this = this;
        var urlData = JSON.parse(_this.$route.query.data);
        if(urlData.status == "0000"){
          _this.paysuccess = true;
          _this.paylose = false;
        }else{
          _this.paysuccess = false;
          _this.paylose = true;
        }
      },
      methods:{
        payBodyBack:function () {
          this.$router.go(-1);
        },
        lian:function () {
          var _this = this;
          _this.serviceShow = true;
        },
        servicehide:function () {
          this.serviceShow = false;
        },
        seen:function () {
          this.$router.push({
            path:"/usercenter/payrecords"
          });
        },
        backroom:function () {
          this.$router.push({
            path:"/"
          });
        },
        againpay:function () {
          this.$router.push({
            path:"/usercenter/mydiamond"
          });
        }
      }
    }
</script>



